import React, { useState, useEffect } from 'react';
import './OrdersView.css';

function OrdersView() {
  const [orders, setOrders] = useState([]);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [loading, setLoading] = useState(false);
  const [statusFilter, setStatusFilter] = useState('ALL');

  //   
  const formatDate = (dateString) => {
    try {
      const date = new Date(dateString);
      const now = new Date();
      const diffMs = now - date;
      const diffMins = Math.floor(diffMs / 60000);
      const diffHours = Math.floor(diffMs / 3600000);
      const diffDays = Math.floor(diffMs / 86400000);

      //   1     
      if (diffMins < 1) {
        return '  ';
      }
      // 60  
      if (diffMins < 60) {
        return ` ${diffMins}  `;
      }
      // 24  
      if (diffHours < 24) {
        return ` ${diffHours}  `;
      }
      // 7  
      if (diffDays < 7) {
        return ` ${diffDays}  `;
      }
      //  
      const absolute = `${date.toLocaleDateString('mn-MN')} ${date.toLocaleTimeString('mn-MN', { hour: '2-digit', minute: '2-digit' })}`;
      return ` ${absolute}`;
    } catch (e) {
      return dateString;
    }
  };

  const formatDateWithAbsolute = (dateString) => {
    const relative = formatDate(dateString);
    try {
      const date = new Date(dateString);
      const absolute = `${date.toLocaleDateString('mn-MN')} ${date.toLocaleTimeString('mn-MN', { hour: '2-digit', minute: '2-digit' })}`;
      return `${relative}  ${absolute}`;
    } catch (e) {
      return relative;
    }
  };

  useEffect(() => {
    fetchOrders();
    const interval = setInterval(fetchOrders, 3000);
    return () => clearInterval(interval);
  }, []);

  const fetchOrders = async () => {
    try {
      const response = await fetch('https://oyushop.onrender.com/api/orders');
      const data = await response.json();
      setOrders(data);
    } catch (err) {
      console.error(':', err);
    }
  };

  const handleSelectOrder = async (order) => {
    setSelectedOrder(order);
  };

  const closeDetails = () => {
    setSelectedOrder(null);
  };

  const handleDeleteOrder = async (orderId) => {
    if (!window.confirm('   ?')) {
      return;
    }

    try {
      const response = await fetch(`https://oyushop.onrender.com/api/orders/${orderId}`, {
        method: 'DELETE'
      });

      if (response.ok) {
        fetchOrders();
        alert('  ');
      }
    } catch (err) {
      alert('  ');
    }
  };

  const handleUpdateStatus = async (orderId, status) => {
    try {
      const response = await fetch(`https://oyushop.onrender.com/api/orders/${orderId}/status`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ status })
      });

      if (response.ok) {
        fetchOrders();
      }
    } catch (err) {
      alert('  ');
    }
  };

  const filteredOrders = orders.filter((order) => {
    if (statusFilter === 'ALL') {
      return true;
    }
    const currentStatus = order.status || ' ';
    return currentStatus === statusFilter;
  });

  return (
    <div className="orders-view">
      <div className="orders-header">
        <h2>   ({filteredOrders.length}/{orders.length})</h2>
        <div className="orders-filter">
          <label htmlFor="statusFilter"> :</label>
          <select
            id="statusFilter"
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
          >
            <option value="ALL"> </option>
            <option value=" ">  </option>
            <option value=" ">  </option>
            <option value=""> </option>
            <option value=""> </option>
          </select>
        </div>
      </div>

      {orders.length === 0 ? (
        <div className="no-orders">
          <p>  </p>
        </div>
      ) : (
        <div className="orders-table-wrapper">
          <table className="orders-table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {filteredOrders.map(order => (
                <tr key={order._id}>
                  <td className="customer-name">{order.customerName}</td>
                  <td>{order.phone}</td>
                  <td className="address-cell">
                    <div>{order.address}</div>
                    {order.notes && (
                      <small className="address-details">
                         {order.notes}
                      </small>
                    )}
                  </td>
                  <td className="price">{order.totalPrice}</td>
                  <td className="date">{formatDateWithAbsolute(order.orderDate)}</td>
                  <td className="status">
                    <span className={`status-badge ${
                      order.status === '' ? 'delivered' : 
                      order.status === '' ? 'cancelled' : 
                      'pending'
                    }`}>
                      {order.status || ' '}
                    </span>
                  </td>
                  <td className="actions">
                    <button 
                      onClick={() => handleSelectOrder(order)}
                      className="detail-btn"
                      title=" "
                    >
                      
                    </button>
                    <select
                      value={order.status || ' '}
                      onChange={(e) => handleUpdateStatus(order._id, e.target.value)}
                      className="status-select"
                      title=" "
                    >
                      <option value=" ">  </option>
                      <option value=" ">  </option>
                      <option value=""> </option>
                      <option value=""> </option>
                    </select>
                    <button 
                      onClick={() => handleDeleteOrder(order._id)}
                      className="delete-btn"
                      title=""
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {selectedOrder && (
        <div className="order-modal">
          <div className="modal-content">
            <button onClick={closeDetails} className="close-btn"></button>
            
            <h3>  </h3>

            <div className="order-details">
              <div className="detail-group">
                <label> :</label>
                <p>{selectedOrder.customerName}</p>
              </div>

              <div className="detail-group">
                <label> :</label>
                <p>{selectedOrder.phone}</p>
              </div>

              <div className="detail-group">
                <label> :</label>
                <p>{selectedOrder.address}</p>
              </div>

              <div className="detail-group">
                <label>  :</label>
                <p>{selectedOrder.notes || '( )'}</p>
              </div>

              <div className="detail-group">
                <label> :</label>
                <p>{formatDateWithAbsolute(selectedOrder.orderDate)}</p>
              </div>
            </div>

            <h4>  :</h4>
            <div className="products-in-order">
              {selectedOrder.products.map((product, index) => (
                <div key={index} className="product-item">
                  <div className="product-details">
                    <h5>{product.name}</h5>
                    <p className="product-desc">{product.description}</p>
                  </div>
                  <div className="product-qty">
                    <span className="qty">{product.quantity} </span>
                    <span className="unit-price">{product.price}  {product.quantity}</span>
                  </div>
                  <div className="product-total">
                    {product.price * product.quantity}
                  </div>
                </div>
              ))}
            </div>

            <div className="order-total">
              <h4> :</h4>
              <p className="total-amount">{selectedOrder.totalPrice}</p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default OrdersView;
