import React, { useState, useEffect, useRef } from 'react';
import './AdminPanel.css';
import OrdersView from './OrdersView';
import { BrowserMultiFormatReader, DecodeHintType } from '@zxing/library';

function AdminPanel({ onLogout }) {
  const [products, setProducts] = useState([]);
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    price: '',
    category: 'baby',
    image: '',
    images: [],
    stock: ''
  });
  const [editingId, setEditingId] = useState(null);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [imagePreview, setImagePreview] = useState('');
  const [imagePreviews, setImagePreviews] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [showProducts, setShowProducts] = useState(true);
  const [inventoryLogs, setInventoryLogs] = useState([]);
  const [showInventory, setShowInventory] = useState(false);
  const [showOrders, setShowOrders] = useState(true);
  const [editingLogId, setEditingLogId] = useState(null);
  const [barcodeSupport, setBarcodeSupport] = useState(false);
  const [isScanning, setIsScanning] = useState(false);
  const [scanMessage, setScanMessage] = useState('');
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const scanIntervalRef = useRef(null);
  const [inventoryForm, setInventoryForm] = useState({
    productCode: '',
    productName: '',
    importDate: new Date().toISOString().split('T')[0],
    costPrice: '',
    salePrice: '',
    quantity: '',
    cargoPrice: '',
    inspectionCost: '',
    otherCost: ''
  });

  useEffect(() => {
    fetchProducts();
  }, []);

  useEffect(() => {
    if (showInventory) {
      fetchInventoryLogs();
    }
  }, [showInventory]);

  useEffect(() => {
    setBarcodeSupport(true);
    return () => {
      stopCameraScan();
    };
  }, []);

  const fetchProducts = async () => {
    try {
      const response = await fetch('https://oyushop.onrender.com/api/products');
      const data = await response.json();
      setProducts(data);
    } catch (err) {
      console.error(':', err);
    }
  };

  const fetchInventoryLogs = async () => {
    try {
      const response = await fetch('https://oyushop.onrender.com/api/inventory-logs');
      const data = await response.json();
      setInventoryLogs(data);
    } catch (err) {
      console.error(':', err);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (event) => {
        const base64 = event.target.result;
        setFormData(prev => ({
          ...prev,
          image: base64
        }));
        setImagePreview(base64);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleMultipleImagesChange = (e) => {
    const files = Array.from(e.target.files);
    if (files.length > 0) {
      const newPreviews = [];
      const newImages = [];
      let filesProcessed = 0;

      files.forEach(file => {
        const reader = new FileReader();
        reader.onload = (event) => {
          const base64 = event.target.result;
          newImages.push(base64);
          newPreviews.push(base64);
          filesProcessed++;

          if (filesProcessed === files.length) {
            setFormData(prev => ({
              ...prev,
              images: [...(prev.images || []), ...newImages]
            }));
            setImagePreviews(prev => [...prev, ...newPreviews]);
          }
        };
        reader.readAsDataURL(file);
      });
    }
  };

  const removeImage = (index) => {
    setFormData(prev => ({
      ...prev,
      images: prev.images.filter((_, i) => i !== index)
    }));
    setImagePreviews(prev => prev.filter((_, i) => i !== index));
  };

  const stopCameraScan = () => {
    if (scanIntervalRef.current) {
      clearInterval(scanIntervalRef.current);
      scanIntervalRef.current = null;
    }
    if (videoRef.current && videoRef.current.srcObject) {
      const tracks = videoRef.current.srcObject.getTracks();
      tracks.forEach(t => t.stop());
      videoRef.current.srcObject = null;
    }
    setIsScanning(false);
  };

  const decodeWithZXing = async (canvas) => {
    try {
      const reader = new BrowserMultiFormatReader();
      const hints = new Map();
      hints.set(DecodeHintType.POSSIBLE_FORMATS, [
        'CODE_128', 'CODE_39', 'CODE_93',
        'EAN_13', 'EAN_8',
        'UPC_A', 'UPC_E',
        'QR_CODE', 'DATA_MATRIX', 'AZTEC'
      ]);
      const result = await reader.decodeFromImageElement(canvas);
      return result ? result.getText() : null;
    } catch (err) {
      return null;
    }
  };

  const startCameraScan = async () => {
    try {
      stopCameraScan();
      setScanMessage('  ...');
      const stream = await navigator.mediaDevices.getUserMedia({ video: { facingMode: 'environment' } });
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        await videoRef.current.play();
      }
      setIsScanning(true);
      setScanMessage('  ...');

      scanIntervalRef.current = setInterval(async () => {
        if (!videoRef.current || !canvasRef.current) return;
        const video = videoRef.current;
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d');
        canvas.width = video.videoWidth || 640;
        canvas.height = video.videoHeight || 480;
        ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
        const code = await decodeWithZXing(canvas);
        if (code) {
          setInventoryForm(prev => ({ ...prev, productCode: code }));
          setScanMessage('  : ' + code);
          stopCameraScan();
        }
      }, 600);
    } catch (err) {
      console.error('Camera scan error:', err);
      setScanMessage('   .      .');
      stopCameraScan();
    }
  };

  const handleImageUploadForCode = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
    setScanMessage('   ...');
    try {
      const imgURL = URL.createObjectURL(file);
      const img = new Image();
      img.onload = async () => {
        try {
          if (!canvasRef.current) return;
          const canvas = canvasRef.current;
          const ctx = canvas.getContext('2d');
          canvas.width = img.width;
          canvas.height = img.height;
          ctx.drawImage(img, 0, 0);
          const code = await decodeWithZXing(canvas);
          if (code) {
            setInventoryForm(prev => ({ ...prev, productCode: code }));
            setScanMessage('  : ' + code);
          } else {
            setScanMessage(' .   .');
          }
        } finally {
          URL.revokeObjectURL(imgURL);
        }
      };
      img.onerror = () => {
        setScanMessage('  .');
        URL.revokeObjectURL(imgURL);
      };
      img.src = imgURL;
    } catch (err) {
      console.error('Image scan error:', err);
      setScanMessage('   .');
    }
  };

  const handleInventoryInputChange = (e) => {
    const { name, value } = e.target;
    setInventoryForm(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleInventorySubmit = async (e) => {
    e.preventDefault();
    
    try {
      const url = editingLogId 
        ? `https://oyushop.onrender.com/api/inventory-logs/${editingLogId}`
        : 'https://oyushop.onrender.com/api/inventory-logs';
      
      const method = editingLogId ? 'PUT' : 'POST';
      
      const response = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...inventoryForm,
          costPrice: parseFloat(inventoryForm.costPrice),
          salePrice: parseFloat(inventoryForm.salePrice),
          quantity: parseInt(inventoryForm.quantity),
          cargoPrice: parseFloat(inventoryForm.cargoPrice) || 0,
          inspectionCost: parseFloat(inventoryForm.inspectionCost) || 0,
          otherCost: parseFloat(inventoryForm.otherCost) || 0
        })
      });

      const data = await response.json();
      if (data.success) {
        setMessage(editingLogId ? '  ' : '  ');
        setInventoryForm({
          productCode: '',
          productName: '',
          importDate: new Date().toISOString().split('T')[0],
          costPrice: '',
          salePrice: '',
          quantity: '',
          cargoPrice: '',
          inspectionCost: '',
          otherCost: ''
        });
        setEditingLogId(null);
        fetchInventoryLogs();
      } else {
        setMessage(' ' + data.message);
      }
    } catch (err) {
      setMessage('  ');
      console.error(':', err);
    }
  };

  const handleEditInventoryLog = (log) => {
    setInventoryForm({
      productCode: log.productCode,
      productName: log.productName,
      importDate: new Date(log.importDate).toISOString().split('T')[0],
      costPrice: log.costPrice.toString(),
      salePrice: log.salePrice.toString(),
      quantity: log.quantity.toString(),
      cargoPrice: (log.cargoPrice || 0).toString(),
      inspectionCost: (log.inspectionCost || 0).toString(),
      otherCost: (log.otherCost || 0).toString()
    });
    setEditingLogId(log._id);
    window.scrollTo({ top: document.querySelector('.inventory-form').offsetTop - 100, behavior: 'smooth' });
  };

  const handleCancelEdit = () => {
    setEditingLogId(null);
    setInventoryForm({
      productCode: '',
      productName: '',
      importDate: new Date().toISOString().split('T')[0],
      costPrice: '',
      salePrice: '',
      quantity: '',
      cargoPrice: '',
      inspectionCost: '',
      otherCost: ''
    });
  };

  const handleDeleteInventoryLog = async (id) => {
    if (!window.confirm('   ?')) return;

    try {
      const response = await fetch(`https://oyushop.onrender.com/api/inventory-logs/${id}`, {
        method: 'DELETE'
      });

      if (response.ok) {
        fetchInventoryLogs();
        setMessage('  ');
      }
    } catch (err) {
      setMessage('  ');
    }
  };

  const handleExportCSV = async () => {
    try {
      const response = await fetch('https://oyushop.onrender.com/api/inventory-logs/export/csv');
      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `baraanyg-burtgel-${new Date().toISOString().split('T')[0]}.csv`;
      a.click();
      window.URL.revokeObjectURL(url);
    } catch (err) {
      setMessage('   ');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage('');

    // Validation
    if (!formData.name || !formData.description || !formData.price || formData.stock === '') {
      setMessage('    ');
      setLoading(false);
      return;
    }

    try {
      const submitData = {
        ...formData,
        price: parseInt(formData.price),
        stock: parseInt(formData.stock)
      };

      if (editingId) {
        // 
        const response = await fetch(`https://oyushop.onrender.com/api/products/${editingId}`, {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(submitData)
        });

        if (response.ok) {
          setMessage('   ');
          setEditingId(null);
          resetForm();
          fetchProducts();
        } else {
          setMessage('   ');
        }
      } else {
        // 
        const response = await fetch('https://oyushop.onrender.com/api/products', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(submitData)
        });

        if (response.ok) {
          setMessage('   ');
          resetForm();
          fetchProducts();
        } else {
          const errorData = await response.json();
          setMessage(` ${errorData.message || '  '}`);
        }
      }
    } catch (err) {
      setMessage('   ');
      console.error('Error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    if (window.confirm('     ?')) {
      try {
        const response = await fetch(`https://oyushop.onrender.com/api/products/${id}`, {
          method: 'DELETE'
        });

        if (response.ok) {
          setMessage('   ');
          fetchProducts();
        }
      } catch (err) {
        setMessage('  ');
      }
    }
  };

  const handleStockChange = async (id, change) => {
    const product = products.find(p => p._id === id);
    if (!product) return;
    
    const newStock = Math.max(0, (product.stock || 0) + change);
    
    try {
      const response = await fetch(`https://oyushop.onrender.com/api/products/${id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ ...product, stock: newStock })
      });

      if (response.ok) {
        fetchProducts();
      }
    } catch (err) {
      setMessage('   ');
    }
  };

  const handleEdit = (product) => {
    setFormData({
      name: product.name,
      description: product.description,
      price: product.price,
      category: product.category,
      image: product.image,
      images: product.images || [],
      stock: product.stock || ''
    });
    setImagePreview(product.image);
    setImagePreviews(product.images || []);
    setEditingId(product._id);
  };

  const resetForm = () => {
    setFormData({
      name: '',
      description: '',
      price: '',
      category: 'baby',
      image: '',
      images: [],
      stock: ''
    });
    setEditingId(null);
    setImagePreview('');
    setImagePreviews([]);
  };

  return (
    <div className="admin-panel">
      <header className="admin-header">
        <h1>  </h1>
        <button onClick={onLogout} className="logout-btn"></button>
      </header>

      <div className="admin-content">
        <div className="form-section">
          <div className="form-header">
            <h2>{editingId ? '  ' : '   '}</h2>
            <button 
              type="button"
              onClick={() => setShowForm(!showForm)} 
              className="toggle-form-btn"
            >
              {showForm ? ' ' : ' '}
            </button>
          </div>

          {message && <div className={`message ${message.includes('') ? 'success' : 'error'}`}>{message}</div>}

          {showForm && (
          <form onSubmit={handleSubmit} className="admin-form">
            <div className="form-group">
              <label>  *</label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                placeholder=" :  "
                required
              />
            </div>

            <div className="form-group">
              <label> *</label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                placeholder="  "
                rows="3"
                required
              />
            </div>

            <div className="form-row">
              <div className="form-group">
                <label> () *</label>
                <input
                  type="number"
                  name="price"
                  value={formData.price}
                  onChange={handleInputChange}
                  placeholder="25000"
                  required
                />
              </div>

              <div className="form-group">
                <label> *</label>
                <input
                  type="number"
                  name="stock"
                  value={formData.stock}
                  onChange={handleInputChange}
                  placeholder="10"
                  required
                />
              </div>

              <div className="form-group">
                <label> *</label>
                <select
                  name="category"
                  value={formData.category}
                  onChange={handleInputChange}
                  required
                >
                  <option value="baby">  </option>
                  <option value="moms">   </option>
                </select>
              </div>
            </div>

            <div className="form-group">
              <label> </label>
              <div className="image-upload-section">
                <input
                  type="file"
                  accept="image/*"
                  onChange={handleImageChange}
                  className="image-file-input"
                  id="imageInput"
                />
                <label htmlFor="imageInput" className="image-upload-label">
                      
                </label>
              </div>
              {imagePreview && (
                <div className="image-preview-wrapper">
                  <p className="preview-label">   :</p>
                  <img src={imagePreview} alt="Preview" className="image-preview" />
                </div>
              )}
            </div>

            <div className="form-group">
              <label>  ( )</label>
              <div className="image-upload-section">
                <input
                  type="file"
                  accept="image/*"
                  multiple
                  onChange={handleMultipleImagesChange}
                  className="image-file-input"
                  id="multipleImagesInput"
                />
                <label htmlFor="multipleImagesInput" className="image-upload-label">
                      ( )
                </label>
              </div>
              {imagePreviews.length > 0 && (
                <div className="multiple-images-preview">
                  <p className="preview-label">  ({imagePreviews.length}):</p>
                  <div className="images-grid">
                    {imagePreviews.map((img, index) => (
                      <div key={index} className="image-preview-item">
                        <img src={img} alt={`Preview ${index + 1}`} />
                        <button 
                          type="button"
                          onClick={() => removeImage(index)}
                          className="remove-image-btn"
                        >
                          
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            <div className="form-buttons">
              <button type="submit" disabled={loading} className="submit-btn">
                {loading ? '...' : editingId ? ' ' : ' '}
              </button>
              {editingId && (
                <button type="button" onClick={resetForm} className="cancel-btn">
                   
                </button>
              )}
            </div>
          </form>
          )}
        </div>

        <div className="products-section">
          <div className="products-header">
            <h2>   ({products.filter(p => 
              p.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
              p.description.toLowerCase().includes(searchQuery.toLowerCase())
            ).length})</h2>
            <div className="products-header-actions">
              <input
                type="text"
                placeholder="  ..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="search-input"
              />
              <button 
                type="button"
                onClick={() => setShowProducts(!showProducts)} 
                className="toggle-products-btn"
              >
                {showProducts ? ' ' : ' '}
              </button>
            </div>
          </div>

          {showProducts && (
          <div className="products-table-wrapper">
            <table className="products-table">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {products
                  .filter(p => 
                    p.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                    p.description.toLowerCase().includes(searchQuery.toLowerCase())
                  )
                  .map(product => (
                  <tr key={product._id}>
                    <td>
                      <strong>{product.name}</strong>
                      <br />
                      <small>{product.description}</small>
                    </td>
                    <td>
                      {product.category === 'baby' ? '' : ''} {product.category}
                    </td>
                    <td className="price">{product.price}</td>
                    <td className="stock">
                      <div className="stock-controls">
                        <button 
                          onClick={() => handleStockChange(product._id, -1)}
                          className="stock-btn minus-btn"
                          title=" "
                        >
                          
                        </button>
                        <span className="stock-value">{product.stock || 0}</span>
                        <button 
                          onClick={() => handleStockChange(product._id, 1)}
                          className="stock-btn plus-btn"
                          title=" "
                        >
                          +
                        </button>
                      </div>
                    </td>
                    <td className="actions">
                      <button 
                        onClick={() => handleEdit(product)} 
                        className="edit-btn"
                        title=""
                      >
                        
                      </button>
                      <button 
                        onClick={() => handleDelete(product._id)} 
                        className="delete-btn"
                        title=""
                      >
                        
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          )}

          {products.length === 0 && showProducts && (
            <p className="no-products">  </p>
          )}
        </div>
      </div>

      <div className="admin-section">
        <div className="section-header">
          <h2>  </h2>
          <button 
            type="button"
            onClick={() => setShowOrders(!showOrders)} 
            className="toggle-orders-btn"
          >
            {showOrders ? ' ' : ' '}
          </button>
        </div>
        
        {showOrders && <OrdersView />}
      </div>
      
      <div className="admin-section">
        <div className="section-header">
          <h2>  </h2>
          <button 
            type="button"
            onClick={() => setShowInventory(!showInventory)} 
            className="toggle-inventory-btn"
          >
            {showInventory ? ' ' : ' '}
          </button>
        </div>

        {showInventory && (
          <>
            <form onSubmit={handleInventorySubmit} className="inventory-form">
              <h3> {editingLogId ? '  ' : ' '}</h3>
              
              <div className="form-row">
                <div className="form-group">
                  <label> *</label>
                  <input 
                    type="text" 
                    name="productCode"
                    value={inventoryForm.productCode}
                    onChange={handleInventoryInputChange}
                    placeholder="-001"
                    required
                  />
                  <div className="scanner-actions">
                    <button
                      type="button"
                      className="scan-btn"
                      onClick={startCameraScan}
                      disabled={isScanning && barcodeSupport}
                    >
                        
                    </button>
                    <label className="scan-upload">
                        
                      <input type="file" accept="image/*" onChange={handleImageUploadForCode} />
                    </label>
                  </div>
                  <div className="scanner-status">
                    <small>{scanMessage || '     '}</small>
                  </div>
                  <div className="scanner-preview">
                    <video ref={videoRef} className={isScanning ? 'video-active' : ''} muted playsInline></video>
                    <canvas ref={canvasRef} style={{ display: 'none' }}></canvas>
                  </div>
                </div>
                <div className="form-group">
                  <label> *</label>
                  <input 
                    type="text" 
                    name="productName"
                    value={inventoryForm.productName}
                    onChange={handleInventoryInputChange}
                    placeholder=" "
                    required
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>  *</label>
                  <input 
                    type="date" 
                    name="importDate"
                    value={inventoryForm.importDate}
                    onChange={handleInventoryInputChange}
                    required
                  />
                </div>
                <div className="form-group">
                  <label>  ()*</label>
                  <input 
                    type="number" 
                    name="costPrice"
                    value={inventoryForm.costPrice}
                    onChange={handleInventoryInputChange}
                    placeholder="25000"
                    required
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>  ()*</label>
                  <input 
                    type="number" 
                    name="salePrice"
                    value={inventoryForm.salePrice}
                    onChange={handleInventoryInputChange}
                    placeholder="35000"
                    required
                  />
                </div>
                <div className="form-group">
                  <label>*</label>
                  <input 
                    type="number" 
                    name="quantity"
                    value={inventoryForm.quantity}
                    onChange={handleInventoryInputChange}
                    placeholder="10"
                    required
                  />
                </div>                <div className="form-group">
                  <label>  ()</label>
                  <input 
                    type="number" 
                    name="cargoPrice"
                    value={inventoryForm.cargoPrice}
                    onChange={handleInventoryInputChange}
                    placeholder="5000"
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>  ()</label>
                  <input 
                    type="number" 
                    name="inspectionCost"
                    value={inventoryForm.inspectionCost}
                    onChange={handleInventoryInputChange}
                    placeholder="2000"
                  />
                </div>
                <div className="form-group">
                  <label>  ()</label>
                  <input 
                    type="number" 
                    name="otherCost"
                    value={inventoryForm.otherCost}
                    onChange={handleInventoryInputChange}
                    placeholder="1000"
                  />
                </div>              </div>

              <button type="submit" className="submit-btn">
                {editingLogId ? ' ' : ' '}
              </button>
              {editingLogId && (
                <button 
                  type="button"
                  onClick={handleCancelEdit}
                  className="cancel-btn"
                >
                   
                </button>
              )}
            </form>

            <div className="inventory-report">
              <div className="report-header">
                <h3>   </h3>
                <button onClick={handleExportCSV} className="export-btn"> Excel  </button>
              </div>

              {inventoryLogs.length === 0 ? (
                <p className="no-data">  </p>
              ) : (
                <div className="inventory-table-wrapper">
                  <table className="inventory-table">
                    <thead>
                      <tr>
                        <th> </th>
                        <th></th>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th></th>
                        <th> </th>
                        <th> </th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {inventoryLogs.map(log => {
                        const cargoPrice = log.cargoPrice || 0;
                        const inspectionCost = log.inspectionCost || 0;
                        const otherCost = log.otherCost || 0;
                        const totalCost = (log.costPrice * log.quantity) + cargoPrice + inspectionCost + otherCost;
                        const totalRevenue = log.salePrice * log.quantity;
                        const totalProfit = totalRevenue - totalCost;
                        
                        return (
                          <tr key={log._id}>
                            <td className="code">{log.productCode}</td>
                            <td>{log.productName}</td>
                            <td>{new Date(log.importDate).toLocaleDateString('mn-MN')}</td>
                            <td className="price">{log.costPrice}</td>
                            <td className="price">{log.salePrice}</td>
                            <td className="quantity">{log.quantity}</td>
                            <td className="cost">{totalCost}</td>
                            <td className="revenue">{totalRevenue}</td>
                            <td className={totalProfit >= 0 ? 'profit' : 'loss'}>
                              {totalProfit >= 0 ? '+' : ''}{totalProfit}
                            </td>
                            <td>
                              <button 
                                onClick={() => handleEditInventoryLog(log)}
                                className="edit-btn"
                                title=""
                              >
                                
                              </button>
                              <button 
                                onClick={() => handleDeleteInventoryLog(log._id)}
                                className="delete-btn"
                                title=""
                              >
                                
                              </button>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          </>
        )}
      </div>
      
      <footer className="footer">
        <p> 2026.Oyu online delguur.      .</p>
      </footer>
    </div>
  );
}

export default AdminPanel;
